@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');

html,
body {
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;



  .card,
  .btn,
  .list-group {
    border-radius: 0em !important;
  }


  /** RESPONSIVE FONT SIZE  WORK-OUT */
  h1 {
    //font-size: 2.5rem ;
    font-size: clamp(1.5rem, 5vw, 2.5rem) !important;
  }

  h2 {
    //font-size: 2rem;
    font-size: clamp(1rem, 5vw, 2rem) !important;
  }

  h3 {
    font-size: clamp(1rem, 5vw, 1.75rem) !important;
  }

  h4 {
    //font-size: 1.5rem;
    font-size: clamp(1rem 5vw, 1.5rem) !important;
  }

  h5 {
    //font-size: 1.25rem;
    font-size: clamp(0.9rem, 5vw, 1.25rem) !important;
  }

  h6 {
    //font-size: 1rem;
    font-size: clamp(0.8rem, 4vw, 1rem) !important;
  }

  p,
  .btn,
  label {
    //font-size: 1rem;
    font-size: clamp(0.75rem, 3vw, 1rem) !important;
  }

  .extra-small-font {
    font-size: 0.5rem !important;
  }

  .small-font {
    font-size: 0.65rem !important;
  }

  .medium-font {
    font-size: clamp(0.65rem, 2vw, 0.8rem) !important;
  }

  .display-1 {
    //font-size: 6rem;
    font-size: clamp(1.2rem, 10vw, 6rem) !important;
  }

  .display-2 {
    //font-size: 5.5rem;
    font-size: clamp(1rem, 10vw, 5.5rem) !important;
  }

  .display-3 {
    //font-size: 4.5rem;
    font-size: clamp(1rem, 10vw, 4.5rem) !important;
  }

  .display-4 {
    //font-size: 3.5rem;
    font-size: clamp(0.9rem, 10vw, 3.5rem) !important;
  }

  .bold {
    font-weight: 500 !important;
  }

  .bolder {
    font-weight: 900 !important;
  }

  ul {
    font-weight: 100 !important;
  }

  a,
  a:hover {
    text-decoration: none !important;
  }


  /** FORM **/
  input,
  textarea,
  select,
  input:focus,
  textarea:focus,
  input.form-control:focus {
    border-radius: 0px !important;
    outline: none !important;
    outline-style: none !important;
  }

  label {
    font-weight: 100;
    line-height: 0.1rem;
    letter-spacing: 0.1rem;
  }

  button {
    outline: none !important;
  }

}